        <div>
            <h1>请选择你喜欢的分类</h1>
            <div
                v-for="(item, index) in list"
                :key="index"
                class="group">
                <span
                    v-for="(category, index2) in item"
                    :key="category.category"
                    :class="category.className"
                    @click="click(category.category, index, index2)"
                >{{ category.category }}</span>
            </div>
            <!-- <div class="group">
                <span
                    :class="{ small: true, checked: checked['美食'] }"
                    @click="click('美食')"
                >历史</span>
            </div>
            <div class="group">
                <span class="small">情感</span>
                <span class="big">科技</span>
