                <span class="small">影视</span>
            </div> -->
        </div><div class="sub-box">
            <button
                :class="{
                    submit: true,
                    'submit-checked': Object.keys(checked).length > 0,
                }"
                @click="submit"
            >我选好了</button>
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { getInterest, postInterest } from '@/api/user';
import { toast, quitCurrentWebview } from '@/utils/jsApiV2/client';

export default {
    name: 'MyHobby',
    components: {
        NavBar,
    },
    methods: {
        submit() {
            const select = Object.keys(this.checked);
            if (!select.length) {
                toast('请先选择喜好！');
                return;
            }

            this.$o2o({
                e_c: '新手任务',
                e_a: '曝光',
                e_n: '我选好了',
            });

            postInterest({ categories: JSON.stringify(select) }).then((res) => {
                if (res.data.status === 200) {
                    toast(res.data.msg || '选择成功');
                    setTimeout(() => {
                        quitCurrentWebview();
                    }, 0);
                    return;
                }

                toast(res.data.msg || '选择失败');
            }).catch(() => {
                toast('选择失败');
            });
        },
        click(key, index, index2) {
            console.log(key, index, this.list[index]);
            if (this.checked[key]) {
                this.$delete(this.checked, key);
                this.list[index][index2].className.checked = false;
            } else {
                this.$set(this.checked, key, true);
                this.list[index][index2].className.checked = true;
            }
        },
        showError() {
            this.$error(this.$route.name);
        },
        getItem(item, checked, small = true, big = false) {
            return {
                category: item.category,
                className: { small, big, checked: !!checked[item.category] },
            };
        },
        setData(data) {
            data.interestList.map((item, index) => { // eslint-disable-line
                if (item.inSelected) {
                    this.$set(this.checked, item.category, true);
                }

                if (index === 0) {
                    this.list[0].push(this.getItem(item, this.checked));
                } else if (index <= 4) {
                    if (index === 3 || index === 2) {
                        this.list[1].push(this.getItem(item, this.checked, false, true));
                    } else {
                        this.list[1].push(this.getItem(item, this.checked));
                    }
                } else if (index <= 7) {
                    this.list[2].push(this.getItem(item, this.checked, false, true));
                } else if (index <= 11) {
                    if (index === 10 || index === 9) {
                        this.list[3].push(this.getItem(item, this.checked, false, true));
                    } else {
                        this.list[3].push(this.getItem(item, this.checked));
                    }
                } else if (index <= 12) {
                    this.list[4].push(this.getItem(item, this.checked));
                }
            });
        },
    },
    created() {
        getInterest().then((res) => {
            console.log(res);
            if (res.data.status === 200) {
                this.$o2o({
                    e_c: '新手任务',
                    e_a: '曝光',
                    e_n: '我的喜好',
                });
                this.setData(res.data.data);
                this.$root.hidePlaceholder();
                return;
            }

            this.showError();
        }).catch((e) => {
            console.error(e);
            this.showError();
        });
    },
    data() {
        return {
            checked: {

            },
            list: [
                [], [], [], [], [],
            ],
        };
    },
};
</script>

